nav__menu ul {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  gap: 60px;\r\n  list-style: none;\r\n}\r\n\r\n.nav__menu ul li {\r\n  padding: 0;\r\n}\r\n\r\n.nav__menu a {\r\n  text-decoration: none;\r\n  font-size: 16px;\r\n  padding: 0.9rem;\r\n}\r\n\r\n.nav__menu ul li:hover {\r\n  color: var(--secondary-color);\r\n  text-decoration: underline;\r\n}\r\n\r\n/* hero */\r\n\r\n.hero-container {\r\n  min-height: 380px;\r\n  position: relative;\r\n\r\n  \r\n}\r\n\r\n.hero-container .hero-content-container {\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  text-align: center;\r\n}\r\n\r\n.hero-container .hero-content-container > h1,\r\np {\r\n  font-weight: bold;\r\n  line-height: 2em;\r\n}\r\n.hero-text {\r\n  color: var(--third-color);\r\n}\r\n\r\n/* Content */\r\nsection {\r\n  margin-top: 40px;\r\n}\r\n\r\na,\r\np,\r\nh1,\r\nh2,\r\nh3,\r\nh4 {\r\n  text-decoration: none;\r\n}\r\n\r\n.main-resto_container {\r\n  margin: 60px auto;\r\n}\r\n\r\n.main-resto_label {\r\n  margin: auto;\r\n  width: max-content;\r\n  font-size: 22px;\r\n}\r\n\r\n.main-resto_label hr {\r\n  margin-top: 0;\r\n  width: 50%;\r\n  border-bottom: 1px solid var(--secondary-color);\r\n}\r\n\r\n.list-resto {\r\n  margin-top: 50px;\r\n  text-align: left;\r\n  display: grid;\r\n  grid-gap: 16px;\r\n}\r\n\r\n.resto-item {\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  width: 100%;\r\n  border-radius: 15px;\r\n  overflow: hidden;\r\n  position: relative;\r\n  top: 0;\r\n  transition: top ease 0.4s;\r\n  cursor: pointer;\r\n}\r\n\r\n.resto-item:hover {\r\n  top: -4px;\r\n}\r\n\r\n.resto-title {\r\n  display: flex;\r\n  justify-content: space-between;\r\n}\r\n\r\n.resto-item_image {\r\n  object-fit: cover;\r\n  width: 100%;\r\n  height: 250px;\r\n  margin-bottom: 15px;\r\n}\r\n\r\n.resto-item_city {\r\n  font-size: 16px;\r\n  text-transform: uppercase;\r\n  color: var(--secondary-color);\r\n  padding: 0 16px 0 16px;\r\n}\r\n\r\n.resto-item_rating {\r\n  color: var(--secondary-color);\r\n  font-weight: 600;\r\n  text-decoration: none;\r\n  margin: 0 0 0 5px;\r\n  padding: 2px 3px 2px 3px;\r\n  border-radius: 5px;\r\n}\r\n\r\n.resto-item_name {\r\n  font-weight: bold;\r\n  font-size: 24px;\r\n  margin-top: 16px;\r\n  transition: 0.3s opacity;\r\n  padding: 0 16px 0 16px;\r\n}\r\n\r\n.resto-item_name:hover {\r\n  opacity: 0.7;\r\n}\r\n\r\n.resto-item_name {\r\n  text-decoration: none;\r\n  color: var(--secondary-color);\r\n  font-size: 24px;\r\n}\r\n\r\n.resto-item_desc {\r\n  display: -webkit-box;\r\n  -webkit-box-orient: vertical;\r\n  -webkit-line-clamp: 5;\r\n  font-weight: 500;\r\n  text-overflow: ellipsis;\r\n  overflow: hidden;\r\n  margin: 16px 15px;\r\n  font-size: 14px;\r\n  line-height: 1.5em;\r\n  color: black;\r\n}\r\n\r\n/* Footer */\r\nfooter {\r\n  text-align: center;\r\n  width: 100%;\r\n  background-color: var(--secondary-color);\r\n  padding: 30px 0;\r\n  line-height: 3em;\r\n}\r\n.footer-text {\r\n  color: var(--third-color);\r\n}\r\n\r\n#root {\r\n  padding-top: 65px;\r\n}\r\n\r\n.restaurant {\r\n  width: 100%;\r\n}\r\n\r\n.restaurant-detail__wrapper h4 {\r\n  margin: 0;\r\n}\r\n\r\n.restaurant-detail__rating {\r\n  margin-right: 15px;\r\n}\r\n\r\n.restaurant-detail__category {\r\n  border-radius: 15px;\r\n  font-size: 12px;\r\n  padding: 5px 20px;\r\n  color: white;\r\n  background-color: var(--decoration-color);\r\n}\r\n\r\n.restaurant-detail__thumbnail {\r\n  width: 100%;\r\n}\r\n\r\n.restaurant-detail__info,\r\n.restaurant-detail__menu-list,\r\n.restaurant-detail__review {\r\n  margin-bottom: 5px;\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\r\n  gap: 50px;\r\n  max-width: 750px;\r\n  margin: 0 auto;\r\n}\r\n\r\n.restaurant-detail__foods > li,\r\n.restaurant-detail__drinks > li {\r\n  list-style: none;\r\n  margin: 10px 15px;\r\n  padding: 1px;\r\n  color: var(--secondary-color);\r\n  text-align: left;\r\n}\r\n\r\n.foods,\r\n.drinks {\r\n  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,\r\n    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\r\n  padding-top: 10px;\r\n}\r\n\r\n.detail {\r\n  display: grid;\r\n  grid-gap: 1.545em;\r\n  font-size: 1em;\r\n  margin-top: 1em;\r\n  text-align: center;\r\n}\r\n\r\n.detail-img {\r\n  width: 100%;\r\n  height: 100%;\r\n  object-fit: cover;\r\n  object-position: center;\r\n}\r\n\r\n.detail-name-address-rating {\r\n  display: inline-block;\r\n  margin: 0;\r\n}\r\n\r\n.icon-primary {\r\n  color: black;\r\n}\r\n\r\n.detail-desc {\r\n  text-align: justify;\r\n  color: var(--primary-color);\r\n  margin-bottom: 0.5em;\r\n  margin: 0;\r\n  font-size: 14px;\r\n  line-height: 1.5em;\r\n  font-weight: 400;\r\n}\r\n\r\n.category {\r\n  padding: 0.3em 1em;\r\n  color: var(--third-color);\r\n  background-color: var(--secondary-color);\r\n  margin-right: 2px;\r\n  border: 1px solid transparent;\r\n  border-radius: 5px 5px 5px 5px;\r\n  text-align: left;\r\n  height: 50px;\r\n}\r\n\r\n.detail-info {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: space-evenly;\r\n  padding-left: 0;\r\n  margin-bottom: 0;\r\n}\r\n\r\n.restaurant-item__not__found {\r\n  margin-top: 30px;\r\n  text-align: center;\r\n}\r\n\r\n.resto-item_image-container {\r\n  max-width: 600px;\r\n}\r\n\r\n.img-container {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.detail-info h4 {\r\n  padding-top: 20px;\r\n  color: var(--secondary-color);\r\n  font-size: 1.5em;\r\n  margin: 0;\r\n  text-align: left;\r\n}\r\n\r\n.detail-info li {\r\n  color: var(--secondary-color);\r\n  position: relative;\r\n  display: block;\r\n  margin-top: 10px;\r\n}\r\n\r\n.resto-name {\r\n  font-size: 18px;\r\n  text-align: left;\r\n}\r\n.resto-address,\r\n.resto-rating,\r\n.resto-category {\r\n  font-size: 14px;\r\n  margin: 0;\r\n  text-align: left;\r\n}\r\n\r\n/*explore restaurant label */\r\n\r\n.explore-restaurant__label {\r\n  font-size: 32px;\r\n  font-weight: bold;\r\n  text-align: center;\r\n  color: var(--secondary-color);\r\n}\r\n\r\n.explore-restaurant__label::after {\r\n  content: '';\r\n  margin-top: 16px;\r\n  display: block;\r\n  border-bottom: 3px solid var(--secondary-color);\r\n}\r\n\r\n#explore-restaurant-list {\r\n  display: grid;\r\n  grid-row-gap: 1.5em;\r\n  padding-top: 1.5em;\r\n}\r\n\r\n/* Like */\r\n.like {\r\n  width: 55px;\r\n  height: 55px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: var(--secondary-color);\r\n\r\n  position: fixed;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  border-radius: 50%;\r\n  border: 0;\r\n\r\n  font-size: 18px;\r\n  color: white;\r\n  cursor: pointer;\r\n}\r\n\r\n/* FORM */\r\nform {\r\n  margin: 2em 0;\r\n  padding: 1.5em;\r\n  border: 3px solid var(--secondary-color);\r\n  border-radius: 15px;\r\n}\r\n\r\n.form-control {\r\n  display: block;\r\n  width: 100%;\r\n  height: 60px;\r\n  padding: 0.738em;\r\n  font-size: 1.37em;\r\n  color: var(--secondary-color);\r\n  background-color: var(--third-color);\r\n  border: 1px solid var(--secondary-color);\r\n  border-radius: 1em;\r\n}\r\n\r\n.form-label {\r\n  display: block;\r\n  font-size: 1.3em;\r\n  margin-bottom: 0.5em;\r\n  text-align: left;\r\n  color: var(--secondary-color);\r\n}\r\n\r\n.mb-3 {\r\n  margin-bottom: 1.3em;\r\n  margin-top: 0.5em;\r\n}\r\n\r\n.btn {\r\n  display: block;\r\n  width: 100px;\r\n  height: 50px;\r\n  font-size: 1.37em;\r\n  color: var(--third-color);\r\n  background-color: var(--secondary-color);\r\n  border: 2px solid var(--secondary-color);\r\n  border-radius: 1em;\r\n}\r\n\r\n.btn:hover {\r\n  box-shadow: 0px 10px 13px -7px var(--secondary-color),\r\n    5px 5px 15px 5px var(--secondary-color);\r\n}\r\n\r\n/* Detail Review */\r\n.detail-review {\r\n  display: grid;\r\n  grid-template-columns: 1fr 1fr;\r\n  gap: 20px;\r\n  max-width: 100%;\r\n  font-size: 1em;\r\n  text-align: center;\r\n}\r\n\r\n.detail-review-item {\r\n  color: var(--secondary-color);\r\n  background-color: var(--third-color);\r\n  margin-bottom: 1.5em;\r\n  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,\r\n    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\r\n  border-radius: 2em;\r\n}\r\n\r\n.header-review {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 0.5em;\r\n  color: var(--secondary-color);\r\n  border-bottom: 1px solid var(--secondary-color);\r\n}\r\n\r\n.name-review {\r\n  font-weight: bold;\r\n  font-size: 1.5em;\r\n  margin: 0;\r\n}\r\n\r\n.date-review {\r\n  font-size: 1em;\r\n  font-weight: lighter;\r\n  font-size: 1em;\r\n}\r\n\r\n.body-review {\r\n  padding: 1em;\r\n  text-align: left;\r\n  font-size: 1.37em;\r\n}\r\n"],"sourceRoot":""}]);
